import { useState } from "react";
import {AnimatePresence, motion}  from "framer-motion";
import {HiChevronDown,HiChevronUp} from "react-icons/hi";
export default function Skill({name,percent,isOpen,stack}) {
    const [open,setOpen]=useState(isOpen?true:false);
    const openBox=(e)=>{
        setOpen(open?false:true);
        console.log("Isopen : ",open);
    }
    return (<div onClick={openBox} className="flex flex-col justify-center items-center w-full bg-white bg-opacity-30 py-2 mb-1 px-2 rounded-md">   
            <div className="flex w-full items-center justify-center">
                <p className="font-bold">{name}</p>
                <div className="w-full ml-2 h-2 bg-orange-50 rounded-full">
                    <div style={{width:`${percent}%`}} className="h-full bg-orange-900 rounded-full"></div>
                </div>
                {open?<HiChevronUp color="black" size="30px"/>:<HiChevronDown color="black" size="30px"/>}
            </div>
            <AnimatePresence>
                {open && <motion.div exit={{y:-50,opacity:0}} initial={{y:-50,opacity:0}} animate={{y:0,opacity:1}} transition={{duration:1}} className="w-full ml-5 mr-3 flex items-end justify-end flex-wrap">
                    {stack.map((e)=><p className="bg-orange-900 px-4 py-1 m-1 rounded-full text-white text-xs font-bold inline-block">{e}</p>)}
                </motion.div>}
            </AnimatePresence>
        
    </div>)
}